.App{
  width:100%;
  display:flex;
  flex-direction:column;
  justify-content:space-between;
  min-height: 100%;
}

.App-header{
  position:fixed;
  width:100%;
  background-color: #262B38;
  display:flex;
  
  align-items:center;
  z-index:1;
}
.App-navbar{
  flex:0.5
}
.App-routes{
  flex:1;
}
.App-header>.App-logo{
  color:#fff;
  flex:0.5;
}
.App-logo>img{
  margin-left: 50px;
  height:7vh;
}
.App-navbar >ul{
  display:flex;
  justify-content:space-around;
  padding:0;
}
.App-navbar>ul>li{
  list-style-type:none;
}

/* .App-navbar>ul>li:hover{
  font-weight:bold;
} */
.App-link{
  display:block;
  color:white;
}
.App-link:hover{
  color:#F0D229;
}
.App-active{
  color:#F0D229;
  font-weight:bold;

}
.toggle-button{
  position:absolute;
  top:.75rem;
  right:1rem;
  display:none;
  flex-direction: column;
  width:30px;
  height:33px;
}
.toggle-button .bar{
  height:10px;
  width:100%;
  margin-bottom:9px;
  background-color:#fff;
  border-radius:10px;
}
.App-footer{
  display:flex;
  min-height:10vh;
  background-color:#E1C8EF;
  justify-content:space-between;
  flex:0;
}
.App-social{
  flex:0.3;
  display:flex;
  justify-content:space-between;
}
.App-social>a{
  margin:5px;
}

.App-social>a.youtube{
  color:#FF0000
}
.App-social>a.facebook{
  color:#38529A
}
.App-social>a.instagram{
  color:#D81D5A
}
.App-social>a.discord{
  color:#5562EA
}
.App-social>a.twitter{
  color:#009DED
}
.App-social>a:hover,.App-social>a:active{
  color:black;
}
@media (max-width: 420px) {
  .App-logo>img{
    margin-left: 0px;
    
  }
  .toggle-button{
    display:flex;
  }
  .App-navbar ul {
    display:none;
  }
  .App-header{
    flex-direction:column;
    align-items:flex-start
  }
  .App-navbar>ul{
    flex-direction:column;
    margin-left:20px;
  }
  .App-navbar>ul>li{
    margin-top:10px;
  }
   #toggle{
    display:flex;
    
  }
  .App-header{
    background-color:rgba(48,43,56,0.99);
  }
  .App-footer{
    flex-direction:column;
    align-items:flex-start;
  }
  .App-social{
    width:100%;
  }
}