.About{ 
 min-height:80vh;
 background-size:cover;
 display: flex;
 flex-direction:column;
}
.About-container{
    background-image: url("https://coolbackgrounds.io/images/backgrounds/index/compute-ea4c57a4.png");
    background-size: cover;
    color:white;
    display: flex;
}
.About-header{
    display:flex;
    margin-top:100px;
    margin-bottom:50px;
    width:100%;
    justify-content:space-between;
    align-items: center;
}
.About-title>img{
    width:390px;
    max-width: 100%;
    border-radius:90%;
}
.About-title{
    flex:0.5;
}
.About-data{
    flex:0.5;
}
.About-data>.About-aboutus{
    color:#F0D229;
    padding:5px;
    margin-bottom:20px;
}

.About-members{
    display:flex;
    justify-content:space-between;
    margin-top:10px;
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
  }
.About-intro{
    display:flex;
    flex-direction:column;
    background-color:#C6C5C7;
}
.About-info{
    padding:5px;
}
.About-member{
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    border: 1px solid #C6C5C7;
    border-radius:10px;
    padding:5px;
    max-width:280px;
    margin-bottom:20px;
    background-color: #D8DBE4;
    align-items: center;
}
.card-image>img{
   width:280px;
   height:200px;
   object-fit: cover;
}
.card-footer{
    width:70%;
    display:flex;
    margin-top:5px;
    justify-content:space-between;
}
.card-footer>a.youtube{
    text-decoration: none;
    color:#FF0000;
}
.card-footer>a.facebook{
    text-decoration: none;
    color:#38529A;
}
.card-footer>a.instagram{
    text-decoration: none;
    color:#D81D5A;
}
.card-footer>a:hover,.card-footer>a:active{
    color:black;
}
@media (max-width: 420px) {
    .About-header{
        flex-direction:column;
    }
    .About-data{
        margin-top:20px;
    }
    .About-members{
        flex-direction: column;
        align-items:center;
    }
}